('p',{staticClass:"mt-3"},[_vm._v("Las personas ya no pueden ser vistas como seres racionales que se comportan de forma sistemática y ordenada a la hora de buscar información, teniendo en cuenta su papel crítico en la web. Al respecto muchos de los sitios tienen en la barra de navegación etiquetas o secciones con los siguientes títulos (Inicio, servicios, nosotros, acerca de y contactos), esta distribución del contenido que hace parte de la arquitectura de la información de un sistema interactivo software debe evaluarse, primeramente.")])]),_c('div',{staticClass:"col-md-4 p-4"},[_c('figure',{staticClass:"p-5"},[_c('img',{staticClass:"imagen_flotante_animada",attrs:{"src":require("@/assets/curso/tema1/3.svg"),"alt":"Avatar"}})])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Patrón tradicional para el diseño de navegación de un sitio Web"}},[_c('div',{staticClass:"col-md-8 mb-4 mb-md-0 p-4"},[_c('p',[_vm._v("Se debe tener siempre presente que el diseño de la navegación influye en gran medida en la experiencia de los usuarios al interactuar con el sitio. Por un lado, los diseñadores deben dedicar el tiempo necesario para familiarizarse con los patrones de búsqueda de sus usuarios, sus emociones, reacciones, etc. Por otro, les ayudará a decidir mejor qué tipo de navegación se desea y es la más adecuada para el sitio y escoger entre varias opciones disponibles.")]),_c('div',{staticClass:"row mt-5 p-3",staticStyle:{"background-color":"#4DD0E1","border-radius":"10px"}},[_c('div',{staticClass:"col-md-8"},[_c('h4',{staticClass:"text-white mt-2"},[_vm._v("Navegación Web")])]),_c('div',{staticClass:"col-md-4 indicador__container text-center",staticStyle:{"transform":"none"}},[_c('button',{staticClass:"color-acento-botones",on:{"click":function($event){_vm.modal1 = true}}},[_vm._v("Ver imagen")]),(_vm.mostrarIndicador)?_c('div',{staticClass:"indicador--click"}):_vm._e()])]),_c('ModalA',{attrs:{"abrir-modal":_vm.modal1},on:{"update:abrirModal":function($event){_vm.modal1=$eve